.App-logo {
  animation: App-logo-spin infinite 2s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}
 .n2w-container {
   display:flex;
   height:100vh;
   width:100%;
   justify-content: center;
   align-items: center;

   .n2w{
     padding: 24px;
     height:400px;
     width:600px;
     background: linear-gradient(45deg, #cc9e9e, #b279dc);
     box-shadow:7px 7px 7px rgba(0,0,0,0.12);
     border-radius: 8px;
     display:flex;
     flex-direction: column;
     .number-input {
       flex: 0 0 100px;
       display:flex;

       input{
        width:100%;
        height: 40px;
        font-size:28px;
        text-align: center;
        border-color: transparent;
        border-radius:4px;
        &:focus{
          outline:unset;
          outline-offset:unset;
        }
      }
     }

     .text-container {
       color: white;
       font-size: 22px;
       flex: 1 1 auto;
       background: transparent;
       border-width:unset;
       border-style:unset;
       &:focus{
         outline:unset;
         outline-offset:unset;
       }
     }

     .copy {
       flex: 0 0 60px;
       background: linear-gradient(to right, #e845ff, #7345ff);
       display:flex;
       justify-content: center;
       align-items: center;
       color:white;
       font-size: 20px;
       font-weight: 700;
       border-radius: 4px;
       text-transform: uppercase;
       cursor: pointer;
     }
   }
 }

 @media all and (max-width:768px){
  .n2w-container{

    .n2w{
      width:80%;
      padding:16px;
      height:80%;
    }
  }
}
